* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  flex-direction: column;
  max-width: 1000px;
  min-height: 100vh;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

#datetime-picker {
  background-color: #fff;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  margin-top: 30px;
  margin-right: 15px;
  padding: 12px 16px;
  font-size: 16px;
  transition: all .3s;
  box-shadow: 0 2px 4px #0000000d;
}

#datetime-picker:focus {
  border-color: #3f51b5;
  outline: none;
  box-shadow: 0 0 0 3px #3f51b533;
}

button[data-start], button[data-stop] {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  border-radius: 8px;
  margin: 15px;
  padding: 12px 28px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 2px 4px #0000001a;
}

button[data-start] {
  color: #fff;
  background-color: #3f51b5;
}

button[data-stop] {
  color: #fff;
  background-color: #f50057;
}

button[data-start]:hover, button[data-stop]:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #00000026;
}

button[data-start]:active, button[data-stop]:active {
  transform: translateY(0);
}

button[data-start]:disabled, button[data-stop]:disabled {
  color: #9e9e9e;
  cursor: not-allowed;
  box-shadow: none;
  background-color: #e0e0e0;
  transform: none;
}

.timer {
  background-color: #fff;
  border-radius: 16px;
  justify-content: center;
  gap: 30px;
  margin-top: 40px;
  padding: 30px;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.field {
  background-color: #f8f9fa;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  min-width: 100px;
  padding: 15px;
  display: flex;
}

.value {
  color: #3f51b5;
  margin-bottom: 8px;
  font-family: Arial, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 1;
}

.label {
  text-transform: uppercase;
  color: #757575;
  letter-spacing: 1.5px;
  font-size: 14px;
  font-weight: 600;
}

@media (width <= 768px) {
  .timer {
    gap: 15px;
    padding: 20px;
  }

  .field {
    min-width: 80px;
    padding: 10px;
  }

  .value {
    font-size: 36px;
  }

  .label {
    font-size: 12px;
  }
}
